<template>
  <div class="masterpieceCard">
    <a :href="`https://www.bilibili.com/video/av${data.aid}`">
      <img :src="paths.imgUrl + data.pic" style="width: 100%;border-radius: 15px;" alt="">
    </a>
    <a :href="`https://www.bilibili.com/video/av${data.aid}`">
      <p class="oneLineP">{{data.title}}</p>
    </a>
    <div class="masterpieceStat">
      <div style="width: 45%;float:left;height: 100%">
        <img class="icon" src="../assets/icons/views.png" alt="">{{data.stat.view | splitNum}}
      </div>
      <div style="width: 45%;float:left;height: 100%">
        <img class="icon" src="../assets/icons/comments.png" alt="">{{data.stat.danmaku | splitNum}}
      </div>
    </div>
  </div>
</template>

<script>
  import paths from "./path.js"
  export default {
    name: "masterpieceCard",
    props: ["data"],
    data(){
      return{
        paths:paths
      }
    }
  }
</script>

<style>
  .masterpieceCard {
    width: 30%;
    float: left;
  }

  .masterpieceCard + .masterpieceCard {
    margin-left: 5%;
  }

  .oneLineP {
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-top: 6px;
    margin-bottom: 6px;
  }
  .masterpieceStat{
    width: 100%;
    margin-top: 0;
    margin-bottom: 6px;
    height: 17px;
    padding-left: 5px;
  }
  .icon{
    height: 100%;
    width: auto;
    margin-right: 5px;
    margin-bottom: -4px;
  }
</style>



