import Vue from 'vue'
import App from './App'
import router from './router'
import {Table,TableColumn} from 'element-ui';

import paths from "./components/path.js"

// import VueLazyLoad from 'vue-lazyload'
// Vue.use(VueLazyLoad,{
//   loading:paths.imgUrl + "loading.png"
// })

Vue.use(Table);
Vue.use(TableColumn);

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {App},
  template: '<App/>'
});

Vue.filter("splitNum",function (a) {
  if (isNaN(a)) {
    return 0;
  }
  a = a.toString();
  let l = a.length;
  let i = a.length - 1;
  let res = '';
  for (i; i >= 0; i--) {
    res = a[i] + res;
    if ((l - i) % 3 === 0 && i > 0) {
      res = ',' + res;
    }
  }
  return res;
})




